*{
    box-sizing: border-box;
    font-family: monospace, Georgia, 'Times New Roman', Times, serif;
}
body{
    margin: 0;
    background-color: #0b2434;
}
main{
    background-color: #f5f5f5;
    height: 62vh;
    margin:120px auto;
    max-width: 800px;
    border-radius: 5px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.dice-container{
    display: grid;
    gap:20px;
    grid-template: auto auto / repeat(5,1fr);
}
.dice-face{
    height: 50px;
    width: 50px;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15);
    display: flex;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
    border-radius: 10px;
}
.dice-num{
    font-size: 2rem;
}
.btn{
    height: 50px;
    width: 120px;
    border: none;
    background-color: #5035ff;
    border-radius: 6px;
    color: #fff;
    cursor: pointer;
    font-size: 1.2rem;
}